import { useMutation, useQuery, useQueryClient } from '@tanstack/react-query';
import { instance } from '../../helpers/http';
import { IMatch } from '../../interfaces/match';
import { Box, Button, Card, CardActions, CardContent, Typography } from '@mui/material';
import { notify } from '../../helpers/notify';

interface IFormData {
  homeScore?: number;
  awayScore?: number;
  status?: string;
}

const Home = () => {
  const { isLoading, data } = useQuery({
    queryKey: ['matches'],
    queryFn: () => instance.get(`/match/all/?flat=true&status=Live`).then((res) => res.data),
  });

  const queryClient = useQueryClient();
  const mutation = useMutation({
    mutationFn: ({ matchId, formData }: { matchId: number; formData: IFormData }) => {
      return instance.put(`/match/${matchId}`, formData);
    },
    onSuccess: (response) => {
      queryClient.invalidateQueries({ queryKey: ['matches'] });
      const { data } = response;
      notify.success(data.message);
    },
    onError: (err: any) => {
      notify.error(err.message);
    },
  });

  const handleScoreHome = (match: IMatch) => {
    mutation.mutate({
      matchId: match.id,
      formData: {
        homeScore: match.homeScore + 1,
      },
    });
  };

  const handleScoreAway = (match: IMatch) => {
    mutation.mutate({
      matchId: match.id,
      formData: {
        awayScore: match.awayScore + 1,
      },
    });
  };

  const handleFinishMatch = (match: IMatch) => {
    mutation.mutate({
      matchId: match.id,
      formData: {
        status: 'Finished',
      },
    });
  };

  const handleCancelScoreHome = (match: IMatch) => {
    if (match.homeScore > 0) {
      mutation.mutate({
        matchId: match.id,
        formData: {
          homeScore: match.homeScore - 1,
        },
      });
    }
  };

  const handleCancelScoreAway = (match: IMatch) => {
    if (match.awayScore > 0) {
      mutation.mutate({
        matchId: match.id,
        formData: {
          awayScore: match.awayScore - 1,
        },
      });
    }
  };

  if (isLoading) return 'Loading...';

  return (
    <Box>
      <h1>Live матчі</h1>
      {data.map((match: IMatch) => (
        <Card key={match.id} style={{ marginBottom: '16px' }}>
          <CardContent>
            <Typography gutterBottom variant="h6" component="span">
              {match.homeTeam.name}
            </Typography>{' '}
            <Typography variant="h6" component="span">
              {match.homeScore} - {match.awayScore}
            </Typography>{' '}
            <Typography variant="h6" component="span">
              {match.awayTeam.name}
            </Typography>
            <CardActions>
              <Button variant="contained" size="small" onClick={() => handleScoreHome(match)}>
                {match.homeTeam.name} Забила
              </Button>
              <Button variant="contained" size="small" onClick={() => handleScoreAway(match)}>
                {match.awayTeam.name} Забила
              </Button>
              <Button variant="outlined" size="small" onClick={() => handleFinishMatch(match)}>
                Закінчити матч
              </Button>
            </CardActions>
            <CardActions>
              <Button
                variant="contained"
                disabled={match.homeScore < 1}
                color="error"
                size="small"
                onClick={() => handleCancelScoreHome(match)}>
                {match.homeTeam.name} - Скасувати гол
              </Button>
              <Button
                variant="contained"
                disabled={match.awayScore < 1}
                color="error"
                size="small"
                onClick={() => handleCancelScoreAway(match)}>
                {match.awayTeam.name} - Скасувати гол
              </Button>
            </CardActions>
          </CardContent>
        </Card>
      ))}
    </Box>
  );
};

export default Home;
