import { FC, useState } from 'react';
import Modal from '../../../components/Modal/Modal';
import { Button, FormControl, IconButton, InputLabel, MenuItem, Select, Stack, TextField } from '@mui/material';
import { useMutation, useQueryClient } from '@tanstack/react-query';
import { instance } from '../../../helpers/http';
import { ITeam } from '../../../interfaces/team';
import { Edit } from '@mui/icons-material';
import { notify } from '../../../helpers/notify';

interface Props {
  createModalOpen: boolean;
  closeEditModal: () => void;
  team: ITeam;
}

const EditTeamModal: FC<Props> = ({ createModalOpen, closeEditModal, team }) => {
  const queryClient = useQueryClient();
  const mutation = useMutation({
    mutationFn: (formData: any) => {
      return instance.put(`/team/${team.id}`, formData, {
        headers: {
          'Content-Type': 'multipart/form-data',
        },
      });
    },
    onSuccess: (response) => {
      queryClient.invalidateQueries({ queryKey: ['teams'] });
      const { data } = response;
      notify.success(data.message);
      closeEditModal();
    },
    onError: (err: any) => {
      notify.error(err.message);
    },
  });
  const PaperProps = {
    component: 'form',
    onSubmit: (event: React.FormEvent<HTMLFormElement>) => {
      event.preventDefault();
      const formData = new FormData(event.currentTarget);
      mutation.mutate(formData);
    },
  };

  const [hasLogo, setHasLogo] = useState<boolean>(team.logo ? true : false);
  const removeLogo = () => {
    setHasLogo(false);
  };

  return (
    <Modal
      isOpen={createModalOpen}
      handleClose={closeEditModal}
      title={`Редагувати ${team.name}`}
      isForm
      PaperProps={PaperProps}>
      <div>
        <FormControl fullWidth margin="dense">
          <TextField
            autoFocus
            required
            margin="dense"
            id="name"
            name="name"
            label="Назва клубу"
            type="text"
            fullWidth
            variant="outlined"
            defaultValue={team.name}
          />
        </FormControl>
        <FormControl fullWidth margin="dense">
          <InputLabel id="type-label">Тип команди</InputLabel>
          <Select labelId="type-label" id="type" label="Тип турніру" name="type" defaultValue={team.type}>
            <MenuItem value="club">Клуб</MenuItem>
            <MenuItem value="national">Збірна</MenuItem>
          </Select>
        </FormControl>

        <FormControl fullWidth margin="dense">
          <TextField
            fullWidth
            id="rank"
            name="rank"
            label="Fifa рейтинг"
            type="number"
            variant="outlined"
            defaultValue={team.rank || 0}
          />
        </FormControl>
        <FormControl fullWidth margin="dense">
          {hasLogo ? (
            <Stack direction="row" gap={2}>
              <img
                src={`${process.env.REACT_APP_PUBLIC_URL}/uploads/${team.logo}`}
                width={32}
                height={32}
                alt={team.name}
              />
              <span>
                <IconButton color="primary" size="small" onClick={removeLogo}>
                  <Edit />
                </IconButton>
              </span>
            </Stack>
          ) : (
            <Button variant="contained" component="label">
              Вибрати лого
              <input type="file" accept=".png,.svg,.jpg,.jpeg,.webp" name="logo" id="logo" hidden />
            </Button>
          )}
        </FormControl>
      </div>
    </Modal>
  );
};

export default EditTeamModal;
