import { FC } from 'react';
import Modal from '../../../components/Modal/Modal';
import { Button, FormControl, FormControlLabel, InputLabel, MenuItem, Select, Switch, TextField } from '@mui/material';
import { useMutation, useQueryClient } from '@tanstack/react-query';
import { instance } from '../../../helpers/http';
import { notify } from '../../../helpers/notify';

interface Props {
  createModalOpen: boolean;
  toggleCreateModal: () => void;
}

const CreateTournamentModal: FC<Props> = ({ createModalOpen, toggleCreateModal }) => {
  const queryClient = useQueryClient();
  const mutation = useMutation({
    mutationFn: (formData: any) => {
      return instance.post('/tournament', JSON.stringify(formData));
    },
    onSuccess: (response) => {
      queryClient.invalidateQueries({ queryKey: ['tournaments'] });
      const { data } = response;
      notify.success(data.message);
      toggleCreateModal();
    },
    onError: (err: any) => {
      notify.error(err.message);
    },
  });
  const PaperProps = {
    component: 'form',
    onSubmit: (event: React.FormEvent<HTMLFormElement>) => {
      event.preventDefault();
      const formData = new FormData(event.currentTarget);
      const formJson = Object.fromEntries((formData as any).entries());
      const formJsonSwitch = {
        ...formJson,
        thirdPlaceMatch: formJson.thirdPlaceMatch === 'on',
        hasTable: formJson.hasTable === 'on',
      };
      mutation.mutate(formJsonSwitch);
    },
  };

  return (
    <Modal isOpen={createModalOpen} handleClose={toggleCreateModal} title="Новий турнір" isForm PaperProps={PaperProps}>
      <div>
        <FormControl fullWidth margin="dense">
          <TextField
            autoFocus
            required
            margin="dense"
            id="name"
            name="name"
            label="Назва турніру"
            type="text"
            fullWidth
            variant="outlined"
          />
        </FormControl>
        <FormControl fullWidth margin="dense">
          <InputLabel id="type-label">Тип турніру</InputLabel>
          <Select labelId="type-label" id="type" label="Тип турніру" name="type">
            <MenuItem value="club">Клубний</MenuItem>
            <MenuItem value="national">Збірні</MenuItem>
          </Select>
        </FormControl>
        <FormControl fullWidth margin="dense">
          <TextField
            required
            margin="dense"
            id="groupNumber"
            name="groupNumber"
            label="Кількість груп"
            type="number"
            fullWidth
            variant="outlined"
          />
        </FormControl>
        <FormControl fullWidth margin="dense">
          <TextField
            required
            margin="dense"
            id="groupMatchNumber"
            name="groupMatchNumber"
            label="Кількість матчів у групі"
            type="number"
            fullWidth
            variant="outlined"
          />
        </FormControl>
        <FormControl fullWidth margin="dense">
          <TextField
            required
            margin="dense"
            id="knockoutRound"
            name="knockoutRound"
            label="Кількість матчів у плей-офф"
            type="number"
            fullWidth
            variant="outlined"
          />
        </FormControl>

        <FormControl fullWidth margin="dense">
          <TextField
            required
            margin="dense"
            id="directNextRound"
            name="directNextRound"
            label="Вихід команд у наступний раунд"
            type="number"
            fullWidth
            variant="outlined"
          />
        </FormControl>

        <FormControl fullWidth margin="dense">
          <TextField
            required
            margin="dense"
            id="playoffRound"
            name="playoffRound"
            label="Вихід команд у стикові матчі"
            type="number"
            fullWidth
            variant="outlined"
          />
        </FormControl>

        <FormControl fullWidth margin="dense">
          <FormControlLabel
            control={
              <Switch
                id="thirdPlaceMatch"
                name="thirdPlaceMatch"
                color="primary"
                inputProps={{ 'aria-label': 'primary checkbox' }}
              />
            }
            label="Матч за 3 місце"
          />
        </FormControl>

        <FormControl fullWidth margin="dense">
          <FormControlLabel
            control={
              <Switch id="hasTable" name="hasTable" color="primary" inputProps={{ 'aria-label': 'primary checkbox' }} />
            }
            label="Має таблицю"
          />
        </FormControl>

        <FormControl fullWidth margin="dense">
          <TextField
            margin="dense"
            id="leagues"
            name="leagues"
            label="Кількість ліг"
            type="number"
            fullWidth
            variant="outlined"
          />
        </FormControl>
        <FormControl fullWidth margin="dense">
          <Button variant="contained" component="label">
            Вибрати лого
            <input type="file" accept=".png,.svg,.jpg,.jpeg,.webp" name="logo" id="logo" hidden />
          </Button>
        </FormControl>
      </div>
    </Modal>
  );
};

export default CreateTournamentModal;
