import {
  Button,
  FormControl,
  FormControlLabel,
  IconButton,
  InputLabel,
  MenuItem,
  Select,
  Stack,
  Switch,
  TextField,
} from '@mui/material';
import { ITournament } from '../../../interfaces/tournament';
import { notify } from '../../../helpers/notify';
import Modal from '../../../components/Modal/Modal';
import { useMutation, useQueryClient } from '@tanstack/react-query';
import { instance } from '../../../helpers/http';
import { useState } from 'react';
import { Edit } from '@mui/icons-material';

interface EditTournamentModalProps {
  tournament: ITournament;
  isOpen: boolean;
  onClose: () => void;
}

const EditTournamentModal: React.FC<EditTournamentModalProps> = ({ tournament, isOpen, onClose }) => {
  const queryClient = useQueryClient();
  const mutation = useMutation({
    mutationFn: (formData: any) => {
      console.log('formData', formData);
      return instance.put(`/tournament/${tournament.id}`, JSON.stringify(formData));
    },
    onSuccess: (response) => {
      queryClient.invalidateQueries({ queryKey: ['tournaments'] });
      const { data } = response;
      notify.success(data.message);
      onClose();
    },
    onError: (err: any) => {
      notify.error(err.message);
    },
  });
  const PaperProps = {
    component: 'form',
    onSubmit: (event: React.FormEvent<HTMLFormElement>) => {
      event.preventDefault();
      const formData = new FormData(event.currentTarget);
      const formJson = Object.fromEntries((formData as any).entries());
      const formJsonSwitch = {
        ...formJson,
        thirdPlaceMatch: formJson.thirdPlaceMatch === 'on' ? true : false,
        hasTable: formJson.hasTable === 'on' ? true : false,
      };
      mutation.mutate(formJsonSwitch);
    },
  };

  const [hasLogo, setHasLogo] = useState<boolean>(tournament.logo ? true : false);
  const removeLogo = () => {
    setHasLogo(false);
  };

  return (
    <Modal isOpen={isOpen} handleClose={onClose} title={`Редагувати ${tournament.name}`} isForm PaperProps={PaperProps}>
      <div>
        <FormControl fullWidth margin="dense">
          <TextField
            autoFocus
            required
            margin="dense"
            id="name"
            name="name"
            label="Назва турніру"
            type="text"
            fullWidth
            variant="outlined"
            defaultValue={tournament.name}
          />
        </FormControl>
        <FormControl fullWidth margin="dense">
          <InputLabel id="type-label">Тип турніру</InputLabel>
          <Select labelId="type-label" id="type" label="Тип турніру" name="type" defaultValue={tournament.type}>
            <MenuItem value="club">Клубний</MenuItem>
            <MenuItem value="national">Збірні</MenuItem>
          </Select>
        </FormControl>
        <FormControl fullWidth margin="dense">
          <TextField
            required
            margin="dense"
            id="groupNumber"
            name="groupNumber"
            label="Кількість груп"
            type="number"
            fullWidth
            variant="outlined"
            defaultValue={tournament.groupNumber}
          />
        </FormControl>
        <FormControl fullWidth margin="dense">
          <TextField
            required
            margin="dense"
            id="groupMatchNumber"
            name="groupMatchNumber"
            label="Кількість матчів у групі"
            type="number"
            fullWidth
            variant="outlined"
            defaultValue={tournament.groupMatchNumber}
          />
        </FormControl>
        <FormControl fullWidth margin="dense">
          <TextField
            required
            margin="dense"
            id="knockoutRound"
            name="knockoutRound"
            label="Кількість матчів у плей-офф"
            type="number"
            fullWidth
            variant="outlined"
            defaultValue={tournament.knockoutRound}
          />
        </FormControl>

        <FormControl fullWidth margin="dense">
          <TextField
            required
            margin="dense"
            id="directNextRound"
            name="directNextRound"
            label="Вихід команд у наступний раунд"
            type="number"
            fullWidth
            variant="outlined"
            defaultValue={tournament.directNextRound}
          />
        </FormControl>

        <FormControl fullWidth margin="dense">
          <TextField
            required
            margin="dense"
            id="playoffRound"
            name="playoffRound"
            label="Вихід команд у стикові матчі"
            type="number"
            fullWidth
            variant="outlined"
            defaultValue={tournament.playoffRound}
          />
        </FormControl>

        <FormControl fullWidth margin="dense">
          <FormControlLabel
            control={
              <Switch
                id="thirdPlaceMatch"
                name="thirdPlaceMatch"
                color="primary"
                defaultChecked={tournament.thirdPlaceMatch}
                inputProps={{ 'aria-label': 'primary checkbox' }}
              />
            }
            label="Матч за 3 місце"
          />
        </FormControl>

        <FormControl fullWidth margin="dense">
          <FormControlLabel
            control={
              <Switch
                id="hasTable"
                name="hasTable"
                defaultChecked={tournament.hasTable}
                color="primary"
                inputProps={{ 'aria-label': 'primary checkbox' }}
              />
            }
            label="Має таблицю"
          />
        </FormControl>

        <FormControl fullWidth margin="dense">
          <TextField
            margin="dense"
            id="leagues"
            name="leagues"
            label="Кількість ліг"
            type="number"
            fullWidth
            defaultValue={tournament.leagues}
            variant="outlined"
          />
        </FormControl>

        <FormControl fullWidth margin="dense">
          {hasLogo ? (
            <Stack direction="row" gap={2}>
              <img
                src={`${process.env.REACT_APP_PUBLIC_URL}/uploads/${tournament.logo}`}
                width={32}
                height={32}
                alt={tournament.name}
              />
              <span>
                <IconButton color="primary" size="small" onClick={removeLogo}>
                  <Edit />
                </IconButton>
              </span>
            </Stack>
          ) : (
            <Button variant="contained" component="label">
              Вибрати лого
              <input type="file" accept=".png,.svg,.jpg,.jpeg,.webp" name="logo" id="logo" hidden />
            </Button>
          )}
        </FormControl>
      </div>
    </Modal>
  );
};

export default EditTournamentModal;
