import { FC } from 'react';
import Modal from '../../../components/Modal/Modal';
import { Button, FormControl, InputLabel, MenuItem, Select, TextField } from '@mui/material';
import { useMutation, useQueryClient } from '@tanstack/react-query';
import { instance } from '../../../helpers/http';
import { notify } from '../../../helpers/notify';

interface Props {
  createModalOpen: boolean;
  toggleCreateModal: () => void;
}

const CreateTeamModal: FC<Props> = ({ createModalOpen, toggleCreateModal }) => {
  const queryClient = useQueryClient();
  const mutation = useMutation({
    mutationFn: (formData: any) => {
      return instance.post('/team', formData, {
        headers: {
          'Content-Type': 'multipart/form-data',
        },
      });
    },
    onSuccess: (response) => {
      queryClient.invalidateQueries({ queryKey: ['teams'] });
      const { data } = response;
      notify.success(data.message);
      toggleCreateModal();
    },
    onError: (err: any) => {
      notify.error(err.message);
    },
  });
  const PaperProps = {
    component: 'form',
    onSubmit: (event: React.FormEvent<HTMLFormElement>) => {
      event.preventDefault();
      const formData = new FormData(event.currentTarget);
      mutation.mutate(formData);
    },
  };

  return (
    <Modal isOpen={createModalOpen} handleClose={toggleCreateModal} title="Нова команда" isForm PaperProps={PaperProps}>
      <div>
        <FormControl fullWidth margin="dense">
          <TextField
            autoFocus
            required
            margin="dense"
            id="name"
            name="name"
            label="Назва клубу"
            type="text"
            fullWidth
            variant="outlined"
          />
        </FormControl>
        <FormControl fullWidth margin="dense">
          <InputLabel id="type-label">Тип команди</InputLabel>
          <Select labelId="type-label" id="type" label="Тип турніру" name="type">
            <MenuItem value="club">Клуб</MenuItem>
            <MenuItem value="national">Збірна</MenuItem>
          </Select>
        </FormControl>
        <FormControl fullWidth margin="dense">
          <TextField fullWidth id="rank" name="rank" label="Fifa рейтинг" type="number" variant="outlined" />
        </FormControl>
        <FormControl fullWidth margin="dense">
          <Button variant="contained" component="label">
            Вибрати лого
            <input type="file" accept=".png,.svg,.jpg,.jpeg,.webp" name="logo" id="logo" hidden />
          </Button>
        </FormControl>
      </div>
    </Modal>
  );
};

export default CreateTeamModal;
