import { useQueryClient, useMutation } from '@tanstack/react-query';
import { FC } from 'react';
import { instance } from '../../../helpers/http';
import Modal from '../../../components/Modal/Modal';
import { notify } from '../../../helpers/notify';

interface Props {
  selected: readonly number[];
  resetSelection: () => void;
  isOpen: boolean;
  onClose: () => void;
}

const DeleteTournamentModal: FC<Props> = ({ selected, resetSelection, isOpen, onClose }: Props) => {
  const queryClient = useQueryClient();
  const mutation = useMutation({
    mutationFn: () => {
      const data = { tournamentsIds: selected };
      return instance.delete('/tournament', { data });
    },
    onSuccess: (response) => {
      queryClient.invalidateQueries({ queryKey: ['tournaments'] });
      resetSelection();
      const { data } = response;
      notify.success(data.message);
      onClose();
    },
    onError: (err: any) => {
      notify.error(err.message);
    },
  });

  const handleDelete = () => {
    mutation.mutate();
  };

  return (
    <Modal
      isOpen={isOpen}
      handleClose={onClose}
      title="Видалення турніру"
      actionFunction={handleDelete}
      actionText="Видалити">
      Ви впевнені, що хочете видалити турнір?
    </Modal>
  );
};

export default DeleteTournamentModal;
